
import { defineComponent, ref } from "vue";
import { mission } from "@/interface";
import { useRoute } from "vue-router";
import jsonToObject from "../mixins/forLoopMixin";
import { FontAwesomeIcon } from "@fortawesome/vue-fontawesome";

let countdownInterval: any;
let countdownTimeout: any;

export default defineComponent({
  props: ["missions"],
  emits: ["removeMission", "completeMission"],
  setup(props) {
    const mission = ref<mission>();
    if (props.missions.length > 0) {
      const missionId = +useRoute().params.missionId;
      mission.value = props.missions.find((m: mission) => m.id === missionId);
    }
    return { mission };
  },
  beforeCreate() {
    if (!this.mission) {
      const missionId = +useRoute().params.missionId;

      const url =
        "https://docs.google.com/spreadsheets/d/1JvK8fSGKJp7U0edAsokelElq41jJYotLMz1RYQXVIZ4/gviz/tq?tqx=out:json"; // + encodeURIComponent('select * order by A desc');

      fetch(
        `${url}&sheet=missions&tq=${encodeURIComponent(
          `select * where A = ${missionId}`
        )}`
      )
        .then((res) => res.text())
        .then((rep) => (this.mission = jsonToObject(rep)[0]));
    }
  },
  components: {
    FontAwesomeIcon,
  },
  data() {
    return {
      mission: {} as mission,
      isPlay: false,
      isCompleted: false,
      countdown: 0,
      second: 0,
    };
  },
  methods: {
    removeMission(mission: mission) {
      this.$emit("removeMission", { ...mission });
    },
    play() {
      this.isPlay = true;
      this.countdown = this.second = this.mission.minute * 60;

      countdownInterval = setInterval(() => this.countdown--, 1000);

      countdownTimeout = setTimeout(() => {
        clearInterval(countdownInterval);
        this.isPlay = false;
        this.isCompleted = true;
        this.$emit("completeMission", { ...this.mission });
      }, this.second * 1000 + 1000);
    },
    stop() {
      clearInterval(countdownInterval);
      clearTimeout(countdownTimeout);
      this.isPlay = false;
    },
    getProgressPercent() {
      return `${(this.countdown / this.second) * 100}%`;
    },
  },
});
