
import { defineComponent, ref } from "vue";
import { hero, mission } from "@/interface";
import { formatDate } from "./mixins/formatDateMixin";
import jsonToObject from "./mixins/forLoopMixin";
import fetchPost from "./mixins/fetchMixin";
import { CRUD } from "@/interface";
import router from "./router";
import {
  faArrowLeft,
  faEllipsis,
  faPenNib,
  faTrashCan,
  faPlus,
} from "@fortawesome/free-solid-svg-icons";
import { library } from "@fortawesome/fontawesome-svg-core";

library.add(faArrowLeft, faEllipsis, faPenNib, faTrashCan, faPlus);

export default defineComponent({
  setup() {
    let missions = ref<mission[]>([]);
    let completedMissions = ref<mission[]>([]);
    let hero = ref<hero>({ id: 1, exp: 0 });

    const url =
      "https://docs.google.com/spreadsheets/d/1JvK8fSGKJp7U0edAsokelElq41jJYotLMz1RYQXVIZ4/gviz/tq?tqx=out:json"; // + encodeURIComponent('select * order by A desc');

    fetch(url + "&sheet=missions")
      .then((res) => res.text())
      .then((rep) => (missions.value = jsonToObject(rep)));

    fetch(
      url +
        "&sheet=CompletedMissions&tq=" +
        encodeURIComponent("select * order by A desc")
    )
      .then((res) => res.text())
      .then((rep) => (completedMissions.value = jsonToObject(rep)));

    fetch(url + "&sheet=hero")
      .then((res) => res.text())
      .then((rep) => (hero.value.exp = jsonToObject(rep)[0].exp));

    return { missions, completedMissions, hero };
  },
  data() {
    return {
      onMission: {} as mission,
      firstLevelMaxExp: 10,
      levelIncreaseRate: 1.2,
      fetchUrl:
        "AKfycbztKNrhW3fy8DbcXQoXJOHiVvJNzbN0SkqKeJJoraWp2KC0Tyno9TE3G9LCUZhzz0xipw",
    };
  },
  methods: {
    createMission(mission: mission): void {
      mission.id = Math.max(...this.missions.map((m) => m.id)) + 1;
      fetchPost<mission>(CRUD.create, "Missions", this.fetchUrl, mission);
      this.missions.push(mission);
    },
    editMission(mission: mission): void {
      fetchPost<mission>(CRUD.update, "Missions", this.fetchUrl, mission);
      const index = this.missions.findIndex((m) => m.id === mission.id);
      this.missions[index] = mission;
    },
    removeMission(mission: mission): void {
      fetchPost<mission>(CRUD.delete, "Missions", this.fetchUrl, mission);
      const index = this.missions.findIndex((m) => m.id === mission.id);
      this.missions.splice(index, 1);
      router.push({ name: "home" });
    },
    completeMission(mission: mission): void {
      mission.id =
        Math.max(...this.completedMissions.map((m) => m.id)) + 1 || 1;
      mission.dateCreated = formatDate(new Date());
      fetchPost<mission>(
        CRUD.create,
        "CompletedMissions",
        this.fetchUrl,
        mission
      );
      this.completedMissions.unshift(mission);

      this.hero.exp += mission.point;
      fetchPost<hero>(CRUD.update, "Hero", this.fetchUrl, this.hero);
    },
  },
});
